<template>
	<div class="topiclistdetail">
		<!-- 导航条 -->
		<van-nav-bar fixed left-arrow @click-left="onClickLeft">
			<template #right>
				<van-icon :name="liulanqiIcon" size="24" />
				<van-icon class="weixin" :name="weixinIcon" size="24" />
				<van-icon
					@click="showShare = true"
					name="ellipsis"
					color="#000"
					size="24"
				/>
			</template>
		</van-nav-bar>
		<!-- 内容 -->
		<div class="content-box">
			<div class="banner">
				<img class="auto-img" :src="topic.image" alt="" />
			</div>
			<div class="title-box">
				<div class="head-img">
					<img class="auto-img" src="../assets/icon/jinghao.png" alt="" />
				</div>
				{{ topic.name }}
			</div>
			<div class="user-count">
				<div class="users">
					<div class="head-img" v-for="(img, index) in users" :key="index">
						<img class="auto-img" :src="img.p" alt="" />
					</div>
				</div>
				<div class="count">已有{{ topic.content_count }}人参与</div>
			</div>
			<div class="description">{{ topic.description }}</div>
		</div>
		<waterfall
			:col="col"
			:width="itemWidth"
			:gutterWidth="gutterWidth"
			:data="list"
			@loadmore="loadmore"
		>
			<template>
				<div
					class="cell-item"
					@click="goNotesDetail(item.note.id)"
					v-for="(item, index) in list"
					:key="index"
				>
					<img class="auto-img" :src="item.note.image_u" alt="加载错误" />
					<div class="item-body">
						<div class="item-desc">{{ item.note.title }}</div>
					</div>

					<div class="head">
						<div class="head-img">
							<img class="auto-img" :src="item.note.author.p" alt="" />
						</div>
						<div class="username">{{ item.note.author.n }}</div>
						<div class="like_count">
							<div class="like-img">
								<img class="auto-img" src="../assets/icon/love.png" alt="" />
							</div>
							<div class="like-sum">
								{{ item.note.like_count }}
							</div>
						</div>
					</div>
				</div>
			</template>
		</waterfall>

		<van-share-sheet
			v-model="showShare"
			title="立即分享给好友"
			:options="options"
			@select="onSelect"
		/>
	</div>
</template>

<script>
import { Toast } from "vant";
export default {
	name: "Topiclistdetail",

	data() {
		return {
			id: "",
			offset: 0,
			limit: 20,
			liulanqiIcon: require("../assets/guanzhu/liulanqi.png"),
			weixinIcon: require("../assets/guanzhu/weixin.png"),
			users: [],
			topic: {},
			list: [],
			col: 2,
			page: 1,
			showShare: false,
			options: [
				[
					{ name: "微信", icon: "wechat" },
					{ name: "朋友圈", icon: "wechat-moments" },
					{ name: "微博", icon: "weibo" },
					{ name: "QQ", icon: "qq" },
				],
				[
					{ name: "复制链接", icon: "link" },
					{ name: "分享海报", icon: "poster" },
					{ name: "二维码", icon: "qrcode" },
					{ name: "小程序码", icon: "weapp-qrcode" },
				],
			],
		};
	},
	computed: {
		itemWidth() {
			return 350 * 0.5 * (document.documentElement.clientWidth / 375); // #rem布局 计算宽度
		},
		gutterWidth() {
			return 9 * 0.5 * (document.documentElement.clientWidth / 375); //#rem布局 计算x轴方向margin(y轴方向的margin自定义在css中即可)
		},
	},
	created() {
		this.id = this.$route.params.id;
		this.getData();
		
	},

	methods: {
		onClickLeft() {
			this.$router.go(-1);
		},
		loadmore() {
			this.offset += this.offset + 10;
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/note/topicdetail/" +
					this.id +
					"/" +
					this.offset +
					"/" +
					this.limit,
			})
				.then((result) => {
					
					this.users = result.data.result.topic.users;
					this.topic = result.data.result.topic;
					result.data.result.list.forEach((e) => {
						this.list.push(e);
					});
				})
				.catch((err) => {
					
				});
		},
		getData() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/note/topicdetail/" +
					this.id +
					"/" +
					this.offset +
					"/" +
					this.limit,
			})
				.then((result) => {
					
					this.users = result.data.result.topic.users;
					this.topic = result.data.result.topic;
					this.list = result.data.result.list;
				})
				.catch((err) => {
					
				});
		},
		goNotesDetail(id) {
			this.$router.push({ name: "Notesdetail", params: { id } });
		},
		gitLike() {
			
		},

		onSelect(option) {
			Toast(option.name);
			this.showShare = false;
		},
	},
};
</script>

<style lang="less" scoped>
.topiclistdetail {
	padding-top: 50px;
	.weixin {
		margin: 0 20px;
	}
	.head {
		display: flex;
		align-items: center;
		margin-bottom: 10px;
		.username {
			padding-left: 5px;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			color: #888;
		}
		.head-img {
			width: 15px;
			height: 15px;
			img {
				border-radius: 50%;
			}
		}
		.like_count {
			display: flex;
			align-items: center;
			margin-left: auto;
			.like-img {
				width: 25px;
			}
		}
	}
	.item-desc {
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}
	.punch {
		display: flex;
		align-items: center;
		padding: 5px 5px 0 5px;
		.punch-img {
			width: 15px;
			height: 15px;
		}
		.punch-text {
			color: #888;
		}
	}

	.item-body {
		padding: 5px;
	}

	.notes {
		padding: 0 10px;
		margin-top: 118px;
		margin-bottom: 50px;
	}
	.content-box {
		.banner {
			width: 100%;
		}
	}
	.title-box {
		padding: 10px;
		font-size: 20px;
		font-weight: bold;
		display: flex;
		align-items: center;
		.head-img {
			width: 30px;
			margin-right: 10px;
		}
	}
	.user-count {
		padding: 10px;
		display: flex;
		.users {
			display: flex;
			width: 100px;
			align-items: center;
			.head-img {
				width: 24px;
				border-radius: 50%;
				overflow: hidden;
			}
		}
		.count {
			margin-left: 5px;
			font-size: 14px;
			color: #999;
		}
	}
	.description {
		padding: 0 10px;
		font-size: 15px;
		line-height: 24px;
	}
}
</style>
